import React from 'react'
import styled from "styled-components";

class UiCard extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        console.log("rendering game a");
        return (
            <Card>
                {this.props.children}
            </Card>
        )                     

    }
}

export default UiCard;


const Card = styled.div`
  width:66%;
  padding: 10px;
  margin: 10px;
  background-color: black;
`


