import React from "react";
import Link from 'gatsby-link';

import styled from "styled-components";
import ReactGA from 'react-ga';
// import PostList from './../components/Blog/PostList.js';
import PostListing from './../components/Blog/PostListing.jsx';
import Button from './../components/UI/Button.jsx';
import SignInBtn from './../components/Google/SignInButton.jsx';

import FadeIn from './../components/UI/animations/FadeIn.jsx';
import ListIn from './../components/UI/animations/ListIn.jsx';
import AnimList from './../components/UI/animations/TestAnim.jsx';

import Line from './../components/graphs/Line.jsx';
import TrendsView from './../components/graphs/views/Trends.jsx';

const IndexPage = ({
  data,
}) => {
  const { allContentJson } = data; // data.markdownRemark holds our post data
  const nodes = allContentJson.edges;
  console.log("index nodes");
  console.log(nodes);
  const posts = nodes.map(node => {
    return <p key={node.node.id} >
    <Link key={node.node.id} to={node.node.path}>Post: {node.node.value}</Link>
    </p>
  })

  return (
    <FadeIn>
      <Container>

          <div>
            <Button isSelectable={true} onClick={()=>{
                    ReactGA.event({
                      category: 'Competitive',
                      action: 'Clicked a button',
                      label: 'Play Again',
                      value: 3
                    })
                  }}>
                Event
            </Button>
          </div>
          <TrendsView />
        <Link to="/page-2/">Go to page 2</Link>
        <AnimList />
        <ListIn>
          {nodes.map(p => {
                  let {path, tags, title, date} = p.node;
                  return (
                      <PostListing key={path} post={p.node}/>
                  )
          })}
        </ListIn>
      </Container>
    </FadeIn>
  )
;
};

export default IndexPage;

export const pageQuery = graphql`
  query IndexQuery {
    site{
      siteMetadata{
        title
      }
    },
    allContentJson{
      edges {
        node {
          title,
          date,
          path,
          tags,
          game,
          content{
            type, text, options
          }
        }
      }
    }
  }
`;



const Container = styled.div`
  margin: 3rem auto;
  background-color: azure;
`


