import React from 'react'
import styled from "styled-components";
import Button from './../UI/Button.jsx';
import {connect} from 'react-redux';
import {setSignedIn} from './actions.js';
import {bindActionCreators} from 'redux';
import img from './../../assets/icons/svg/baseline-settings-20px.svg';

class SignInButton extends React.Component{
    constructor(props){
        super(props);
        this.handleAuthClick = this.handleAuthClick.bind(this);
        this.handleSignoutClick = this.handleSignoutClick.bind(this);
    }
    handleAuthClick(event) {
        console.log("signing in")
        gapi.auth2.getAuthInstance().signIn();
        }
    handleSignoutClick(event) {
        console.log("signing out")
        gapi.auth2.getAuthInstance().signOut();
        this.props.setSignedIn(false);
    }
        renderButton(){
            if(this.props.gapi.isSignedIn){
                return <Button onClick={()=>{this.handleSignoutClick()}}>Sign Out</Button>
            }else{
                return <Button onClick={()=>{this.handleAuthClick()}}>Sign In</Button>
                
            }
        }
    // renderButton(){
    //     if(this.props.gapi.isSignedIn){
    //         return <p>Is signed in!@</p>
    //     }else{
    //         return <p>Is not signed in</p>
    //     }
    // }
    render(){
        console.log("signed in? " + this.props.gapi.isSignedIn)
        console.log(this.props);
        return (
            <Container>
                <img class="icon" src={img} alt="my icon"/>
            </Container>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        gapi: state.gapi
    };
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
            setSignedIn
        }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(SignInButton);

const Container = styled.div`
    position: absolute;
    top:0;
    right:0;
    padding: 10px;
`


