import React from 'react';
import ReactDOM from 'react-dom';
import ChartistGraph from 'react-chartist';
// import Chartist from 'chartist';
 
export default class Pie extends React.Component {
  constructor(props){
    super(props);
    var type = 'Line'
    var options = {
      showPoint: false,
      axisX: {
        position: 'start',
        offset: 60
      },

    };
    var seq = 0,
  delays = 80,
  durations = 500;
    var listeners={
      created: function() {
        var seq = 0;
      },
      draw: function(data){
       // console.log("animating data, seq " + seq);
      //  console.log(data);
        seq++;
        if(data.type === 'line') {
          // If the drawn element is a line we do a simple opacity fade in. This could also be achieved using CSS3 animations.
          // data.element.animate({
          //   opacity: {
          //     // The delay when we like to start the animation
          //     begin: seq * delays + 1000,
          //     // Duration of the animation
          //     dur: durations,
          //     // The value where the animation should start
          //     from: 0,
          //     // The value where it should end
          //     to: 1
          //   }
          // });
          if(data.type === 'line' || data.type === 'area') {
            /*
                from: data.path.clone().scale(0, 0)
                .translate(data.chartRect.width(), data.chartRect.height())
                .stringify(),
            */
            data.element.animate({
              d: {
                begin: 500,
                dur: 1000* data.index,
                from: data.path.clone().scale(1, 0)
                .translate(0, data.chartRect.height())
                .stringify(),
                to: data.path.clone().stringify(),
                easing: 'easeOutQuart'
              }
            });
          }
        }else if(data.type === 'grid') {
          // Using data.axis we get x or y which we can use to construct our animation definition objects
          if(data.index === 132){
         //   console.log("animating grid " + data.axis.units.pos + " val " + data[data.axis.units.pos + '1']);
          }
          
          if(data.axis.units.pos === 'x'){
            var pos1Animation = {
              begin: data.index * 50,
              dur: durations,
              from: data[data.axis.units.pos + '1'] - 30,
              to: data[data.axis.units.pos + '1'],
              easing: 'easeOutQuart'
            };
        
            var pos2Animation = {
              begin: data.index * 50,
              dur: durations,
              from: data[data.axis.units.pos + '2'] - 100,
              to: data[data.axis.units.pos + '2'],
              easing: 'easeOutQuart'
            };

            var heightAnim = {
              begin: data.index * 50,
              dur: durations,
              from: data['y2'],
              to: data['y1'],
              easing: 'easeOutQuart'
            }
        
            var animations = {};
            // animations[data.axis.units.pos + '1'] = pos1Animation;
            // animations[data.axis.units.pos + '2'] = pos2Animation;
            animations['y1'] = heightAnim;
            animations['opacity'] = {
              begin: data.index * 50,
              dur: durations,
              from: 0,
              to: 1,
              easing: 'easeOutQuart'
            };
        
            data.element.animate(animations);
          }
          
        }
      }
    }
    let chart = <ChartistGraph data={props.data} listener={listeners} options={props.options} type={'Line'} />;
 //   console.log("line chart");
 //   console.log(chart);
    this.state={
      chart,
      type
    }
  }
  render() {
 
    // var data = {
    //     series: [{
    //         name: 'series-1',
    //         data:[
    //             {x: 1, y: 100},
    //             {x: 2, y: 50},
    //             {x: 3, y: 25},
    //             {x: 25, y: 25},
    //             {x: 80, y: 6.25}
    //             ]   
    //     },{
    //         name: 'series-2',
    //         data:[
    //             {x: 4, y: 10},
    //             {x: 8, y: 55},
    //             {x: 30, y: 33},
    //             {x: 80, y: 18},
    //             {x: 90, y: 65}
    //             ]
    //     }]
    // }
 
    var options = {
      showPoint: false,
      axisX: {
        position: 'start',
        offset: 60
      }
    };
 
    
 
    return (
      <div>
        {this.state.chart}
        {/* <ChartistGraph data={this.props.data} options={this.props.options} type={type} /> */}
      </div>
    )
  }
}




