class TestAnim extends React.Component{
    constructor(props){
        super(props);
        this.state={
            items: ["1", "2", "3"]
        }
        this.addItem = this.addItem.bind(this);
    }
    addItem(){
        this.setState({
            items: [
                ...this.state.items,
                this.state.items.length.toString()
            ]
        })
    }
    render(){
        return (
            <div>
                <button onClick={()=>{
                    this.addItem();
                }}>Add</button>
            <FadeIn>
                {this.state.items.map(i=>{
                    return <p key={i}>item {i}</p>
                })}
            </FadeIn>
            </div>
        )                     

    }
}

export default TestAnim;


const Card = styled.div`
  width:66%;
  padding: 10px;
  margin: 10px;
  background-color: black;
`


