import React from 'react'
import styled from "styled-components";
import {timing, colors} from './animations/Constants.js';

class UiButton extends React.Component{
    constructor(props){
        super(props);
        console.log("UIButton");
        console.log(props);
        this.handleClick = this.handleClick.bind(this);
        this.state={
            isSelectable: this.props.isSelectable? this.props.isSelectable:false,
            selected: false
        }
    }
    handleClick(){
        if(this.props.onClick){
            this.props.onClick();
        }
        if(this.state.isSelectable){
            this.setState({selected: !this.state.selected})
        }
    }
    render(){
        return (
            <Button style={this.state.selected?SelectedStyle:null} onClick={() => {this.handleClick()}}>
                {this.props.children}
            </Button>
        )                     

    }
}

export default UiButton;

const SelectedStyle = {
    backgroundColor: `${colors.accent}`,
    color: `${colors.accentShadow}`,
    border: `4px ${colors.accentShadow} solid`
}

const Button = styled.button`
  padding: 5px 10px 5px 10px;
  background-color: transparent;
  border: 4px ${colors.accent} solid;
  color: ${colors.accent};
  outline: 0;
  transition: all ${timing.mini}s ease-out;
  &:hover {
    color: ${colors.accentShadow};
    border: 4px ${colors.accentShadow} solid;
  }
  &:active {
    color: ${colors.accentShadow};
    border: 4px ${colors.accentShadow} solid;
  }
  &:selected {
    color: ${colors.accentShadow};
  }

`

