import React from 'react'
import styled from "styled-components";
import { CSSTransitionGroup } from 'react-transition-group'

//const label = 'slide-up';
class FadeIn extends React.Component{
    constructor(props){
        super(props);
        console.log("there are " + this.props.children.length + " children");
        let label = 'slide-up';
        let duration = 0.9;
        let delay = 0.2;
        let animation = styled.span`
                .${label}-enter {
                    transform: translateY(0);
                }
            
            .${label}-enter.${label}-enter-active {
                transform: translateY(280);
                transition: transform .5s ease-in;
            }
            
            .${label}-leave {
                opacity: 1;
            }
            
            .${label}-leave.${label}-leave-active {
                opacity: 0.01;
                transition: opacity 300ms ease-in;
            }
            .${label}-appear {
                transform: translateY(100vh) rotate(45deg);
                opacity: 0;
            }
            
            .${label}-appear.${label}-appear-active {
                transform: translateY(0vh) rotate(0deg);
                opacity: 1;
                transition-property: all;
                transition-duration: ${duration}s;
                transition-timing-function: ease-out;
            }
            
            ${this.delayChildren(this.props.children.length, delay)}
            `;
        this.state={
            label: label,
            duration: duration,
            delay: delay,
            animation: animation
        }
    }
    delayChildren(count, delay){
        let css = ``;
        for(let i=1;i<=count; i++){
            console.log("nth child " + i + " delay = " + (i*delay));
            css = css + `div:nth-child(${i}){transition-delay:${i*delay}s;}`;
        }
        console.log("delay children");
        console.log(css);
        return css;
    }
    render(){
        return (
            <this.state.animation>
                <CSSTransitionGroup
                    transitionName={this.state.label}
                    transitionAppear={true}
                    transitionAppearTimeout={this.state.delay*(this.props.children.length+5)*1000 + this.state.duration*2}
                    transitionEnterTimeout={this.state.duration*1000}
                    transitionLeaveTimeout={this.state.duration*1000}>
                        {this.props.children}
                </CSSTransitionGroup>
           </this.state.animation>
        )                     

    }
}

export default FadeIn;


