import React from 'react';
import ReactDOM from 'react-dom';
import Line from './../Line.jsx';
import Adapter from './../../../utils/adapterParallel';
import Trends from './../../../data/trends.json';
// import ChartistGraph from 'react-chartist';
 
/*
    {
      "Month": "2004-02",
      "math: (Worldwide)": 37,
      "physics: (Worldwide)": 22,
      "chemistry: (Worldwide)": 22,
      "biology: (Worldwide)": 18,
      "science: (Worldwide)": 98
    },
*/
export default class Pie extends React.Component {
    constructor(props){
        super(props);
        let order = ['s1', 's2'];
        // let adapters = {
        //     [order[0]]: new Adapter(Trends, 'Math', 'Month', ['math: (Worldwide)', "math: (Worldwide)"]),
        // }
        // console.log("trend line chart");
        // console.log(adapters);
        this.state={
            order,
            adapter: new Adapter(Trends, 'Math', 'Month', ['math: (Worldwide)', "math: (Worldwide)", 
            "physics: (Worldwide)", "chemistry: (Worldwide)", "biology: (Worldwide)", "science: (Worldwide)"])
        }
    }
    orderSeries(){
        return this.state.order.map((order, i) => {
            return this.state.adapters[order].getSeries();
        });
    }
  render() {
 
    // var data = {
    //     series: [
    //         this.state.adapters[this.state.order[0]].getSeries()
    //     ]
    // }
    // var data = {
    //     series: this.orderSeries()
    // }
    // var data = {
    //     labels: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
    //     series: [
    //       [5, 4, 3, 7, 5, 10, 3],
    //       [3, 2, 9, 5, 4, 6, 4]
    //     ]
    // }
    // labelInterpolationFnc: function(value, index) {
    //     return index % 2 === 0 ? value : null;
    //   }
    let fn = (function(){
      //  console.log("called fn");
        let map = new Map();
        return function(value){
            let t = value.slice(0, 4);
           // console.log("label check " + t + " map size " + map.size);
            if(!map.has(t)){
              //  console.log("returning value " + t);
                map.set(t, 1);
                return t;
            }
        }
    })();
    var options = {
        showPoint: false,
      axisX: {
        labelInterpolationFnc: fn
      }
    };
 
    var type = 'Line'
 
    return (
      <div>
        <Line data={this.state.adapter} options={options} type={type} />
      </div>
    )
  }
}


